import { Paper, Box, Grid2, Typography, Divider, Link } from "@mui/material"
import React from "react"
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';

const Footer = () => {
    return (
    <Paper sx={{ borderRadius: '10px'}} elevation={0}>
        <Box sx={{margin: 3, marginX: 10}}>
            <Grid2 container>
                <Grid2 size={{xs:12, md: 5}}>
                    <Typography variant="h6">!</Typography>
                </Grid2>
                <Grid2 size={{xs:12, md: 4}}>
                    <Typography>  </Typography>
                    <Link href="#" color="inherit" sx={{ mr: 2 }}>
                      <FacebookIcon />
                    </Link>
                    <Link href="#" color="inherit">
                        <TwitterIcon />
                    </Link>
                </Grid2>
                <Grid2 size={{xs:12, md: 3}}>
                    <Grid2 container>
                        <Grid2 size={12}>
                         <Link href="#" underline="none" color="inherit"> </Link>
                        </Grid2>
                        <Grid2 size={12}>
                            <Link href="#" underline="none" color="inherit"> </Link>
                        </Grid2>
                        <Grid2 size={12}>
                            <Link href="#" underline="none" color="inherit"> </Link>
                        </Grid2>
                    </Grid2>
                </Grid2>
            </Grid2>
            <Divider sx={{marginTop: 2, marginBottom: 1}}/>
            <Grid2 container>
                <Grid2 size={{xs:12, md: 6}}>
                <Typography variant="body1" sx={{ fontSize: '0.75rem' }}>  , 2025</Typography>
                </Grid2>
                <Grid2 size={{xs:12, md: 3}}>
                <Typography variant="body1" sx={{ fontSize: '0.75rem' }}>
                    <Link href="#" underline="none" color="inherit">
                        
                    </Link>
                </Typography>
                </Grid2>
                <Grid2 size={{xs:12, md: 3}}>
                <Typography variant="body1" sx={{ fontSize: '0.75rem' }}>
                    <Link href="#" underline="none" color="inherit">
                       
                    </Link>
                </Typography>
                </Grid2>
            </Grid2>
        </Box>
    </Paper>)

}

export default Footer;